<template>
	<div class="q-pa-md">
		<q-table
			:data="widgets"
			:columns="columns"
			:pagination="pagination"
			:rows-per-page-label="$t('Widgets per page')"
			>
			
			<template v-slot:header="props">
				<q-tr :props="props">
					<q-th auto-width />
					<q-th
						v-for="col in props.cols"
						:key="col.name"
						:props="props"
						>
						
						{{ col.label }}
					</q-th>
					<q-th auto-width />
				</q-tr>
			</template>
			
			<template v-slot:body="props">
				<q-tr :props="props" :class="{ 'rowDeleted': isDeleted(props.row) }">
					<q-td auto-width>
						<div v-if="isDeleted(props.row) !== true">
							<q-btn size="sm" flat round @click="onWidgetEdit(props.row)" icon="mdi-pencil" />
							<q-btn size="sm" flat round @click="onWidgetCopy(props.row)" icon="mdi-content-copy" />
						</div>
					</q-td>
					<q-td
						v-for="col in props.cols"
						:key="col.name"
						:props="props"
						>
						
						<div :class="{ 'itemDeleted': isDeleted(props.row) }">
							{{ col.value }}
						</div>
					</q-td>
					<q-td auto-width>
						<q-btn v-if="isDeleted(props.row) !== true" size="sm" flat round @click="onWidgetRemove(props.row)" icon="mdi-delete" color="negative" />
						<q-btn v-if="isDeleted(props.row)" size="sm" flat round @click="onWidgetRestore(props.row)" icon="mdi-restore" color="positive" />
					</q-td>
				</q-tr>
			</template>
		</q-table>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

export default defineComponent({
	'name': 'WidgetsTable',
	'props': [ 'widgets' ],
	
	data() {
		return {
			'pagination': {
				'sortBy': 'name',
				'rowsPerPage': 15
			},
			'columns': [{
					'name': 'id',
					'label': this.$i18n.t('ID'),
					'field': 'id',
					'align': 'left',
					'sortable': true,
					'style': {
						'width': '20%'
					}
				}, {
					'name': 'title',
					'label': this.$i18n.t('Title'),
					'field': 'title',
					'align': 'left',
					'sortable': true,
					'style': {
						'width': '30%'
					}
				}, {
					'name': 'module',
					'label': this.$i18n.t('Module'),
					'field': 'module',
					'align': 'left',
					'sortable': true,
					'style': {
						'width': '50%'
					}
				}
			]
		}
	},
	
	'methods': {
		isDeleted(widget) {
			return widget._deleted === true;
		},
		onWidgetEdit(widget) {
			this.$emit('onEditWidget', widget);
		},
		onWidgetCopy(widget) {
			this.$store.commit('editor/copyWidget', { widget });
		},
		onWidgetRemove(widget) {
			this.$store.commit('editor/removeWidget', { widget });
		},
		onWidgetRestore(widget) {
			this.$store.commit('editor/restoreWidget', { widget });
		}
	}
});
</script>
