<template>
	<div class="full-height WidgetsPage">
		<div class="row no-wrap">
			<q-toolbar class="paper" style="min-height: 56px">
				<q-toolbar-title shrink>{{ $t('Widgets') }} ({{ widgetsCount }})</q-toolbar-title>
				
				<q-space />
				<q-btn color="primary" icon="mdi-plus-circle" :label="$t('add widget')" @click="addWidget" />
			</q-toolbar>
		</div>
		
		<!-- WIDGET CONFIGURATION -->
		<widget-page
			@onClose="selectedWidgetId = null"
			:widgetId="selectedWidgetId"
			/>
		
		<!-- WIDGET LIST -->
		<div v-show="!expertMode && group.widgets.length > 0" class="row no-wrap paper" v-for="group in groups" :key="group.id">
			<q-expansion-item
				v-model="expanded[group.id]"
				:icon="group.icon"
				:label="group.label + ' (' + group.widgets.length + ')'"
				class="full-width"
				>
				
				<widgets-table
					@onEditWidget="editWidget"
					:widgets="group.widgets"
					/>
				
			</q-expansion-item>
		</div>
		
		<!-- EXPERT MODE -->
		<paper v-show="expertMode" :style="{ 'height': (pageHeight-66-16) + 'px' }">
			<json-editor id="widgets" :json="json" @onChange="onJsonEditor" />
		</paper>
	</div>
</template>

<script>
import { defineComponent } from '@vue/composition-api';

import JsonEditor from 'src/components/json-editor.vue';
import WidgetsTable from './WidgetsTable.vue';
import WidgetPage from './WidgetPage/WidgetPage.vue';

import { v4 as uuid } from 'uuid';

export default defineComponent({
	'name': 'WidgetsPage',
	
	'components': {
		JsonEditor,
		WidgetsTable,
		WidgetPage
	},
	
	data() {
		return {
			'expanded': {},
			'selectedWidgetId': null
		}
	},
	
	'methods': {
		addWidget() {
			this.selectedWidgetId = uuid();
		},
		editWidget(widget) {
			this.selectedWidgetId = widget.id;
		},
		onJsonEditor(props) {
			if (!props._error) {
				this.$store.commit('editor/set', { [props.id]: JSON.parse(props.value) });
			}
		}
	},
	
	'computed': {
		pageHeight() {
			return this.$store.state.jarvis.pageHeight;
		},
		groups() {
			return this.$store.getters['editor/getGroupedWidgets'];
		},
		widgetsCount() {
			return this.$store.getters['editor/getWidgetsWthoutDeletionsCount'];
		},
		json() {
			return this.$store.state.editor.widgets;
		},
		expertMode() {
			return this.$store.state.editor.settings?.configExpertMode;
		}
	}
});
</script>
